.menu {
  z-index: 99999; // Very high z-index to be above everything
  // Transparent background - pill design handles the background
  background: transparent;
  pointer-events: auto; // Ensure menu is clickable

  @media #{$media-size-phone} {
    position: fixed; // Use fixed instead of absolute to ensure it's above everything
    top: 70px; // Adjust based on header height
    right: 0;
    border: none;
    margin: 0;
    padding: 10px;
    z-index: 99999; // Ensure it's above all content
    pointer-events: auto;
  }

  &__inner {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    max-width: 100%;
    margin: 0 auto;
    padding: 0 15px;
    font-size: 1rem;
    list-style: none;
    position: relative;
    z-index: 100000; // Even higher z-index for inner menu
    pointer-events: auto;

    li {
      margin: 0 12px;
      position: relative;
      z-index: 100001;
      pointer-events: auto;
    }

    @media #{$media-size-phone} {
      flex-direction: column;
      align-items: flex-start;
      padding: 0;
      background: var(--bg-color);
      border-radius: 12px;
      box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);
      min-width: 200px;
      padding: 12px;
      z-index: 100000;
      pointer-events: auto;

      li {
        margin: 0;
        padding: 8px 12px;
        width: 100%;
        z-index: 100001;
        pointer-events: auto;
      }
    }
  }

  &-trigger {
    width: 24px;
    height: 24px;
    fill: currentColor;
    margin-left: 10px;
    cursor: pointer;
    display: none;
    position: relative;
    z-index: 100003; // Very high z-index for trigger
    pointer-events: auto; // Ensure trigger is clickable
    min-width: 44px; // Minimum touch target
    min-height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;

    @media #{$media-size-phone} {
      display: flex;
      z-index: 100003;
      pointer-events: auto;
    }
  }

  a {
    display: inline-block;
    margin-right: 15px;
    text-decoration: none;
    position: relative;
    z-index: 100002; // Highest z-index for links
    pointer-events: auto; // Explicitly enable clicks
    cursor: pointer;

    &:hover {
      text-decoration: underline;
    }

    &:last-of-type {
      margin-right: 0;
    }
    
    @media #{$media-size-phone} {
      display: block;
      width: 100%;
      padding: 8px 12px;
      margin-right: 0;
      margin-bottom: 4px;
      border-radius: 8px;
      z-index: 100002;
      pointer-events: auto;
      min-height: 44px; // Minimum touch target
      display: flex;
      align-items: center;
      
      &:hover {
        background: rgba(79, 195, 247, 0.1);
        text-decoration: none;
      }
    }
  }
}


.submenu {
  background: $light-background-header;

  @media (prefers-color-scheme: dark) {
    background: $dark-background-header;
  }

  @media (prefers-color-scheme: light) {
    background: $light-background-header;
  }

  [data-theme=dark] & {
    background: $dark-background-header;
  }

  [data-theme=light] & {
    background: $light-background-header;
  }

  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
  }

  li a, .dropbtn {
    display: inline-block;
    text-decoration: none;
  }

  li.dropdown {
    display: inline-block;
  }

  .dropdown-content {
    display: none;
    position: absolute;
    background: $dark-background-header;

    @media (prefers-color-scheme: light) {
      background: $light-background-header;
    }
    
    [data-theme=dark] & {
      background: $dark-background-header;
    }
  
    [data-theme=light] & {
      background: $light-background-header;
    }
  }

  .dropdown-content a {
    padding: 12px 20px;
    text-decoration: none;
    display: block;
    text-align: left;
  }

  .dropdown-content a:hover {
    background: $dark-background-header;
    
    @media (prefers-color-scheme: light) {
      background: $light-background-header;
    }

    [data-theme=dark] & {
      background: $dark-background-header;
    }
  
    [data-theme=light] & {
      background: $light-background-header;
    }
  }

  .dropdown:hover .dropdown-content {
    display: block;
  }
}